.Player {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden; }

.Player iframe,
.Player object,
.Player embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%; }

.Player__NotAvailable {
  position: relative; }
  .Player__NotAvailable img, .Player__NotAvailable span {
    transition: opacity .5s; }
  .Player__NotAvailable img {
    opacity: 1; }
  .Player__NotAvailable span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
    opacity: 0; }
  .Player__NotAvailable:hover img {
    opacity: 0.5; }
  .Player__NotAvailable:hover span {
    opacity: 1; }
